.photos {
  background: #e8e8e8;
  h2 {
    display: inline-block;
    margin-bottom: 50px; }
  &__inst {
    text-decoration: none;
    color: $dark-grey-blue-two;
    font-size: 16px;
    font-weight: 600;
    letter-spacing: 1.6px;
    float: right;
    transition: all 0.3s;
    &:hover {
      color: #fff;
      .icon {
        fill: #ffffff; } } }
  .icon {
    fill: $dark-grey-blue-two;
    position: relative;
    top: 3px;
    left: 5px;
    transition: all 0.3s; } }
#instafeed {
  display: flex;
  flex-wrap: wrap;
  a {
    width: 250px;
    height: 250px;
    display: block;
    margin-left: 10px;
    margin-bottom: 10px;
    transition: all 0.3s;
    position: relative;
    img {
      width: 100%; }
    &:hover {
      box-shadow: -12.3px 4.2px 59px 0 rgba(0, 0, 0, 0.48);
      position: relative;
      z-index: 10;
      transform: scale(1.05); } } }
