.thank {
	padding-bottom: 60px;
	&__1 {
		background: #e8e8e8;
		padding-top: 130px;
		padding-bottom: 30px;
		position: relative;
		&:before {
			content: '';
			background: url('/img/checkout/thank.svg') repeat;
			bottom: -5px;
			position: absolute;
			height: 10px;
			width: 100%; }
		h2 {
			margin-bottom: 20px; }
		p {
			margin-bottom: 45px; } }
	&__order {
		&__number {
			font-size: 22px;
			font-weight: 600;
			color: #2e6b67;
			margin-bottom: 30px; }
		&__block {
			width: 740px;
			h3 {
				font-weight: 500;
				font-size: 18px; }
			p {
				font-size: 18px;
				font-weight: 600;
				margin-bottom: 0; }
			hr {
				border: none;
				margin: 15px auto;
				width: 100%;
				border-bottom: 1px solid #d0d0d0; }
			&__sum {
				span {
					font-size: 20px;
					font-weight: 600; }
				.icon {
					width: 12px;
					height: 13px;
					fill: $black; } }
			&__total {
				h3 {
					font-weight: 600; }
				span {
					color: #2d6661; }
				.icon {
					fill: #2d6661; } } } }
	&__2 {
		padding-top: 40px;
		padding-bottom: 40px;
		h3 {
			font-size: 18px;
			font-weight: 500; }
		p {
			font-weight: 600; }
		.thank__order {
			&__info {
				width: 740px;
				hr {
					border: none;
					margin: 15px auto;
					width: 100%;
					border-bottom: 1px solid #d0d0d0; } } } } }
