.checkbox:not(checked) {
  position: absolute;
  opacity: 0;
}

.checkbox:not(checked) + .label {
  position: relative;
  padding: 0 0 0 40px;
}

.checkbox:not(checked) + .label:before {
  content: '';
  position: absolute;
  top: -4px;
