  content: '';
  position: absolute;
  top: 5px;
  left: 8px;
  width: 15px;
  height: 9px;
  border: 3px solid #ffffff;
  border-top: none;
  border-right: none;
  transform: rotate(-45deg);
  transition: all .2s;
  opacity: 0;
  background: transparent !important;
}

.checkbox:checked + .label:before {
  background: #2d6965;
}

.checkbox:checked + .label:after {
  opacity: 1;
