.cause {
	position: relative;
	z-index: 5;
	padding-bottom: 100px;
	&__top {
		height: 135px;
		background-size: cover;
		width: 100%;
		background: url('../img/ibc-landing-svg/cause-top.svg') no-repeat;
		background-size: 120% 100%;
		background-position: 51% 0;
		padding-top: 10px;
		h2 {
			font-size: 24px;
			font-weight: 500;
			color: #fff;
			text-transform: uppercase; } }
	&__content {
		display: flex;
		justify-content: space-around;
		position: relative;
		img {
			position: absolute;
			left: 28%;
			top: 102px;
			width: 440px; }
		&__left, &__right {
			width: 340px;
			h3 {
				font-size: 30px;
				font-weight: 500;
				position: relative;
				padding-top: 100px;
				padding-bottom: 75px;
				padding-left: 55px;
				text-transform: uppercase;
				&:before {
					content: '';
					width: 390px;
					height: 115px;
					background-image: url("../img/ibc-landing-svg/cause.svg");
					background-repeat: no-repeat;
					position: absolute;
					left: -65px;
					bottom: 30px; } }
			p {
				font-size: 16px;
				font-weight: 500;
				padding-bottom: 20px; } }
		&__left {
			margin-right: 100px; }
		&__right {
			h3:first-child {
				padding-left: 40px;
				padding-top: 80px; }
			h3:last-child {
				color: #ff3366;
				position: relative;
				padding-top: 40px;
				&:before {
					background-image: url("../img/ibc-landing-svg/cause-dotted.svg");
					background-repeat: no-repeat; }
				p {
					padding-bottom: 0;
					position: absolute;
					text-transform: none;
					font-size: 16px;
					letter-spacing: 2px;
					span {
						position: absolute;
						width: 30px;
						height: 30px;
						background: #ff3366;
						border-radius: 100px;
						color: #fff;
						display: flex;
						justify-content: center;
						padding-top: 6px;
						padding-left: 2px; } }
				p:nth-child(1) {
					left: -90px;
					bottom: -10px;
					width: 160px;
					span {
						left: 15px;
						top: -65px; } }
				p:nth-child(2) {
					position: absolute;
					right: -115px;
					bottom: -15px;
					width: 265px;
					span {
						left: 5px;
						top: -40px; } }
				p:nth-child(3) {
					position: absolute;
					right: -115px;
					top: -20px;
					width: 160px;
					span {
						left: -45px;
						bottom: -18px; } } } } } }


















